.portfolio{
    &__content{
        padding: 20px;
        width: 100%;

        &__filter{
            display: inline-block;
            margin: 0 auto;
            border-radius: 25px;
            border: 1px dotted var(--yellow-theme-main-color);

            li{
                font-size: 16px;
                display: inline-block;
                cursor: pointer;
                position: relative;
                color: var(--yellow-theme-main-color);
                text-transform: uppercase;
                padding: 12px 20px;
                overflow: hidden;
                transition: all .5s ease;

                &.active{
                    background: var(--yellow-theme-main-color);
                    color: var(--yellow-theme-sub-text-color);
                    font-weight: 600;
                    border-radius: 25px;
                    margin: 3px 3px 2px 3px;
                }
            }
        }

        &__cards{
            display: grid;
            grid-template-columns: repeat(3,1fr);
            grid-auto-rows: auto;
            grid-gap: 1rem;
            margin-top: 10px;

            &__item{
                height: 366px;
                cursor: pointer;
                position: relative;
                background: #fffdec;
                &:hover .overlay{
                    opacity: 1;
                }
                &__img-wrapper{
                    height: 100%;
                    a{
                        height: 100%;

                        img{
                            height: inherit;
                            object-fit: contain;
                            width: 100%;
                        }
                    }
                }
                .overlay{
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    right: 0;
                    left: 0;
                    height: 100%;
                    width: 100%;
                    opacity: 0;
                    transition: .5s ease;
                    background: #2f3919e3;
                    div {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%,-50%);
                        text-align: center;
                        p{
                            color: var(--yellow-theme-sub-text-color);
                            margin: 0px;
                            font-size: 2rem;
                            position: relative;
                          
                            white-space: pre-wrap;
                            max-width: 180px;
                            animation: typingEffect 1s steps(30,end);
                        }
                        button{
                            cursor: pointer;
                            margin-top: 16px;
                            font-size: 2rem;
                            padding: 10px 70px;
                            color: var(--yellow-theme-sub-text-color);
                            border: 1.5px solid ;
                            border-radius: 4px;
                            font-weight: 500;
                            background:  #293804;;
                        }
                    }
                }
            }
        }
    }
}
@media only screen  and (max-width: 480px) {
    .portfolio__content__filter > li{
      font-size: 11px;
    }
    .portfolio__content__cards{
        grid-template-columns: repeat(1,1fr);
    }
}
@media only screen  and (min-width: 481px) and (max-width: 767px) {
    .portfolio__content__filter > li{
      font-size: 11px;
    }
    .portfolio__content__cards{
        grid-template-columns: repeat(2,1fr);

        &__item{

            .overlay{
               div > p {
                font-size: 2rem;
               }
            }
        }
    }
  
}
@keyframes typingEffect {
    
}